import './App.css';
import NavBar from './Components/NavBar';
import About from './Components/About';
import Contact from './Components/Contact';
import Home from './Components/Home';
import Dribble from './Components/Dribble';
import {
  Switch,
  Route,
} from "react-router-dom";
function App() {
  return (
    <div className="App">
      <NavBar/>
      <Switch>
      
          <Route path="/about">
            <About />
          </Route>
          <Route path="/contact">
            <Contact />
          </Route>
          <Route path="/dribble">
            <Dribble />
          </Route>
          <Route exact path="/">
            <Home />
          </Route>
        </Switch>
    </div>
  );
}

export default App;
